.header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background: linear-gradient(to bottom, rgba(135, 206, 235, 0.9), rgba(0, 191, 255, 0.9));
    padding: 1rem;
    z-index: 1000;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 1);
    transition: opacity 0.3s ease-in-out;
}

.header.with-background {
    opacity: 0.95;
}

.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
}

.nav-links {
    display: flex;
    gap: 1rem;
}

.header .nav-links a {
    color: white;
    text-decoration: none;
    transition: color 0.5s ease, text-shadow 0.5s ease;
    cursor: pointer;
    font-weight: 600;
    position: relative;
    text-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}

.header .nav-links a:hover,
.header .nav-links a:focus {
    color: black;
    text-shadow: 0 0 5px rgba(255, 255, 255, 0.5);
}

.header .nav-links a::after {
    content: '';
    position: absolute;
    width: 0;
    height: 2px;
    bottom: -2px;
    left: 0;
    background-color: black;
    transition: width 0.5s ease-in-out;
}

.header .nav-links a:hover::after {
    width: 100%;
}

.donate-button {
    background-color: rgba(255, 105, 10, 0.8);
    color: var(--text-color);
    border: none;
    padding: 0.5rem 1rem;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.5s ease;
    font-weight: 600;
    text-decoration: none;
    font-size: 0.9rem;
}

.donate-button:hover {
    background-color: rgba(255, 47, 0, 0.7);
}

.burger-menu {
    display: none;
    flex-direction: column;
    cursor: pointer;
}

.burger-menu div {
    width: 25px;
    height: 3px;
    background-color: var(--text-color);
    margin: 2px 0;
    transition: all 0.5s ease;
}

@media (max-width: 768px) {
    .nav-links {
        display: none;
    }

    .nav-links.active {
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 100%;
        left: 0;
        right: 0;
        background-color: rgba(135, 206, 235, 0.8);
        padding: 1rem;
        box-shadow: 0 5px 5px rgba(0, 0, 0, 0.8);
    }

    .burger-menu {
        display: flex;
    }

    .donate-button {
        font-size: 0.8rem;
        padding: 0.4rem 0.8rem;
    }
}

/* Glossy effect */
.header {
    background-image: linear-gradient(
            to bottom,
            rgba(55, 198, 255, 0.6) 50%,
            rgba(145, 224, 255, 0.3) 100%
    );
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.4), inset 0 2px 0 rgba(255, 255, 255, 0.4);
}

.donate-button {
    background-image: linear-gradient(
            to bottom,
            rgba(255, 255, 255, 0.5) 50%,
            rgba(255, 255, 255, 0.1) 100%
    );
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.8), inset 0 1px 0 rgba(255, 255, 255, 0.8);
}